.UserInfoCard {
  position: relative;
  text-align: center;
  border-radius: 4px;
}

.UserInfoCard-bg {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  background-color: white;
  background-image: url(/CodeShare/static/media/card-bg.49adb432.png);
  height: 100px;
  background-size: 300px 100px;
}

.UserInfoCard-face-img {
  border-radius: 50%;
  width: 100px;
  margin-top: -50px;
}

.UserInfoCard-username {
  font-size: 24px;
  color: #d4c17e;
}

.UserInfoCard-info {
  border-top: 1px solid rgb(239, 239, 239);
  border-bottom: 1px solid rgb(239, 239, 239);
  margin-top: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-content: stretch;
}

.UserInfoCard-info-number {
  font-size: 18px;
  font-family: 'Dancing Script';
}

.UserInfoCard-info-left, .UserInfoCard-info-mid, .UserInfoCard-info-right {
  width: 33.3333333%;
  font-size: 18px;
  font-family: 'News Cycle';
}

.UserInfoCard-info-mid {
  border-left: 1px solid rgb(239, 239, 239);
  border-right: 1px solid rgb(239, 239, 239);
}

.main {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  background-color: #f0f2f5;
  align-content: stretch;
}

.content, .content-sider {
  border-radius: 4px;
  margin-top: 10px;
  background-color: #fff;
}

.content {
  margin-left: 50px;
  margin-right: 2px;
  min-height: 500px;
  min-width: 500px;
  flex: 500px 1;
}

.content-sider {
  margin-right: 50px;
  margin-left: 2px;
  min-width: 250px;
  max-width: 250px;
  flex: 250px 1;
}
.login-form-wrap {
  margin: 180px auto;
}

.login-form {
  max-width: 300px;
}

.login-form-forgot {
  float: right;
}

.login-form-button {
  width: 100%;
}


